const swiper = new Swiper('.swiper', {
	slidesPerView: 4,
	spaceBetween: 40,
	loop: true,
	navigation: {
		nextEl: '.swiper-button-next',
		prevEl: '.swiper-button-prev',
	},
	watchOverflow: false,
	breakpoints: {
		320: {
			slidesPerView: 1,
		},
		380: {
			slidesPerView: 2,
		},
		768: {
			slidesPerView: 3,
		},
		992: {
			slidesPerView: 4,
		}
	}
});
